@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap);
*{
  margin: 0;
  padding: 0;
}

body{
  width: 100%;
  overflow-x: hidden;
  font-family: 'Poppins', sans-serif;
}

:root{
    --main-color:#f7e732;
    --secondary-color:#037cd7;
    --button-color:#ff9f00;
    --title-color:#ffffff;
    --bold-color:#212121;
}

a:hover{
  text-decoration: none;
}



/* *************************   Navbar start    ******************************** */

.nav{
    width: 100%;
    margin:0px auto;
    height: 55px;
    transition: .3s ease-in-out;
    z-index: 999;
    overflow: hidden;
    background: var(--secondary-color);
}


.nav.active{
  position: fixed !important;
  top: 0;
  background: var(--title-color);
  box-shadow: 0 2px 20px var(--box-shadow);
}

.nav .container{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

  .hamburger{
     display: none;
     margin-right: 10px;
  }

  .hamburger .span1{
    width: 32px;
    height: 2px;
    background: var(--main-color);
    display: block;
}

  .hamburger .span2{
      margin-top: 4px;
      margin-left: auto;
      width: 22px;
      height: 2px;
      background: var(--main-color);
      display: block;
      transition: .3s ease-in;
  }

  .hamburger:hover{
    cursor: pointer;
  }

  .empty{
     display: none;
  }


.logo{
    max-width: 130px;
}


.side-menu ul{
 margin: 0px;
 z-index: 1;
 display: -webkit-flex;
 display: flex;
 -webkit-align-items: center;
         align-items: center;
}


.side-menu ul li{
  display: -webkit-flex;
  display: flex;
  padding:0px;
  margin: 0 30px;
  list-style: none;
  font-size: 15px;
  cursor: pointer;
  color:var(--bold-color);
  transition:.2s;
  font-weight:600;
  text-transform: capitalize;
  position: relative;
  z-index: 2;
}

.side-menu ul a:last-child li{
  margin: 0;
  margin-left: 30px;
}

.side-menu ul li:hover{
  opacity: .8;
}

.nav-link span{
  color: var(--title-color);
  font-weight: 700;   
}

.nav-link-icon{
  font-size: 20px!important;
  color: var(--title-color);
  margin-right: 3px;
  -webkit-align-self: flex-end;
          align-self: flex-end;
}


/* *************  Search bar  **************** */

.search-input-container{
  background: var(--title-color);
  padding: 2px 10px;
  border-radius: 2px;
  box-shadow: 0 5px 8px rgba(0,0,0,.3);
}

.search-input{
  width:400px; 
  padding: 2px 5px;
  background: transparent;
  border: none;
  border-radius: 3px;
  height: 33px;
  outline: none;
  font-size: 16px;
  color: var(--bold-color);
  font-weight: 600;
}

.searchbar-icon{
  color: var(--secondary-color);
}

.search-input::-webkit-input-placeholder{
  font-size: 13px;
  font-weight: 600;
}

.search-input:-ms-input-placeholder{
  font-size: 13px;
  font-weight: 600;
}

.search-input::placeholder{
  font-size: 13px;
  font-weight: 600;
}


.nav-link h6{
  position: absolute;
  right: -12px;
  top: -8px;
  background: var(--main-color);
  width: 20px;
  height: 20px;
  display: grid;
  place-items: center;
  border-radius: 50%;
  font-size: 12px;
  font-weight: 700;
  z-index: -1;
  color: var(--secondary-color);
}




/* *******************  Media query  ************************* */


@media only screen and (max-width:786px){
  
    .nav{
      width: 100%;
      height: auto;
      margin:0px;
      padding: 25px 10px;
      -webkit-justify-content: space-between;
              justify-content: space-between;
  }
  
  .hamburger{
    display:block;
    z-index: 1000;
  }

    .hamburger:hover .span2{
      width: 32px;
  }

  
   .side-menu ul{
    margin: 0;
    position: absolute;
    padding: 0;
    top: 0;
    left:0;
    width: 100%;
    height: 100vh!important;
    background: var(--title-color);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    -webkit-transform: translateX(-120%);
            transform: translateX(-120%);
    transition: .3s ease-in;
    z-index: 999;
  }
  
  .side-menu ul.active{
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  
  .side-menu ul a:last-child li{
    margin: 0;
    margin: initial;
    margin-left: 0;
    margin-left: initial;
  }



   .hamburger .span1.active{
    width: 25px;
    height: 3px;
    -webkit-transform: rotate(44deg) translate(2px,-3px);
            transform: rotate(44deg) translate(2px,-3px);
    opacity: .8;
  }

    .hamburger .span2.active{
      height: 3px;
      margin-top: 0;
      width: 25px;
      display: block;
      -webkit-transform: rotate(-47deg) translateX(5px);
              transform: rotate(-47deg) translateX(5px);
      opacity: .8;
    }

}
