/* ***********************   Quantity select box  *************************** */


.quantity-container{
    position: absolute;
    top:20px;
    right: 25px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 90px;
    height: 35px;
    padding:10px 5px;
    background: #d9dad7;
    overflow: hidden;
    border-radius: .25em;
}

/* Arrow */
.quantity-container::after {
    position: absolute;
    content: "\f078";
    font-family: "Font Awesome 5 free";
    font-weight: 900;
    font-size: 11px;
    top: 0;
    right: 0;
    height: 100%;
    padding: 9px;
    color: var(--bold-color);
    cursor: pointer;
    pointer-events: none;
    transition: .25s all ease;
  }
  /* Transition */
  .quantity-container:hover::after {
    color: var(--secondary-color);
  }
  

.quantity-container label{
    margin: 0;
    font-size: 14px;
    font-weight: 600;
    color: var(--bold-color);
    padding-left: 4px;
}

.quantity-container select {
    -webkit-appearance: none;
    appearance: none;
    outline: 0;
    box-shadow: none;
    border: 0 !important;
    background: #d9dad7;
    background-image: none;
}

/* Remove IE arrow */
.quantity-container select::-ms-expand {
    display: none;
}

.quantity-container select{
    -webkit-flex: 1 1;
            flex: 1 1;
    padding: 0 .5em;
    color: var(--bold-color);
    cursor: pointer;
    font-size: 14px;
    font-weight: 600;
}

